.container{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.container .box{
    /* position: relative; */
    width: 320px;
    height: 320px;
    /* margin: 48px; */
}

.container .box:hover .imgBox{
    transform: translate(-0 ,-56px );
}

.container .box:hover .content{
    transform: translate(56px ,264px );
    /* background-color: #fff; */
    
}

.imgBox{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    transition: all 0.5s ease-in-out;

}

.imgBox img{
    width: 320px;
    height: 320px;
    object-fit: cover;
    resize: both;
    border-radius: 8px;
}
.name{
    font-size: 18px;
}
.position{
    font-size: 12px;
}
